import React from "react"
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";

import Layout from "../components/layout"
import SEO from "../components/seo"

// Components
import FarrowingHeader from "../components/Farrowing/FarrowingHeader/FarrowingHeader.component";
import FarrowingGraph from "../components/Farrowing/FarrowingGraph/FarrowingGraph.component";
import VideoContainer from "../components/Farrowing/VideoContainer/VideoContainer.component";
import ContactUsComp from "../components/ContactUsComp/ContactUsComp.component";
import DividerArrow from "../components/DividerArrow/DividerArrow.component";
import SpanishFlag from "../components/DownloadAsset/SpanishFlag.component";
import "../components/DownloadAsset/DownloadAsset.styles.scss";


const Farrowing = () => {

  const data = useStaticQuery(graphql`
    query {
      farrowingDownloads: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(farrowing/downloads)/"}}) {
        edges {
          node {
            frontmatter {
              title
              mainTitle
              englishLink
              spanishLink
              productImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            html
          }
        }
      }
      farrowingPage: allMarkdownRemark(filter: {frontmatter: {title: {eq: "Farrowing"}}}) {
        edges {
          node {
            frontmatter {
              title
              description
              headImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
    {
      data.farrowingPage.edges.map(({node}) => (
        <div>
          <SEO title={ node.frontmatter.title } description={ node.frontmatter.description } />
          <Img fluid={ node.frontmatter.headImage.childImageSharp.fluid } />
        </div>
      ))
    }
    {
      data.farrowingDownloads.edges.map(({node}) => (
        <div>
          <FarrowingHeader />
          <FarrowingGraph />
          <div className="download-asset-container">
            <DividerArrow />
            <div className="download-asset">
                <Img fluid={node.frontmatter.productImage.childImageSharp.fluid} />
                <div className="download-asset-text">
                    <h2>{ node.frontmatter.mainTitle }</h2>
                    <div dangerouslySetInnerHTML={{ __html: node.html }} />
                    <div className="download-asset-links">
                        <a href={ node.frontmatter.englishLink } target="_blank" rel="noopener noreferrer">Download PDF</a>
                        <a href={ node.frontmatter.spanishLink } target="_blank" rel="noopener noreferrer"><SpanishFlag />Also available in Spanish</a>
                    </div>
                </div>
            </div>
          </div>
          <VideoContainer />
          <ContactUsComp />
        </div>
      ))
    }
    </Layout>
  )
}

export default Farrowing
